.login {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-color: white;
}

.login__logo {
  margin: 20px auto;
  object-fit: contain;
  width: 100px;
}

.login__container {
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}

.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container > form > h5 {
  margin-bottom: 5px;
}

.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: white;
  width: 98%;
  outline: none;
}
.login__container > form > input:focus {
  box-shadow: 0px 0px 7px 0px #f0c14b;
  -webkit-box-shadow: 0px 0px 7px 0px #f0c14b;
  -moz-box-shadow: 0px 0px 7px 0px #f0c14b;
}

.login__container > p {
  margin-top: 15px;
  font-size: 12px;
}

.login__signInButton {
  background-color: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  padding: 2px 5px;
  color: #111;
  display: block;
  width: 98%;
}

.login__registerButton {
  border-radius: 2px;
  width: 100%;
  border: 1px solid darkgray;
  margin-top: 10px;
  height: 30px;
}
